@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400&display=swap);
html,
body {
  margin: 0;
  background-image: url(/static/media/2118876.e3d56bcd.jpg);
  background-size: cover;
}

header {
  background-color: black;
  margin: 0;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  font-size: 20px;
  color: white;
}

nav a {
  color: white;
  text-decoration: none;
  font-family: "Rubik", sans-serif;
  display: inline-block;
}

nav a:hover {
  background-color: #282c34;
}

main > div {
  display: flex;
  flex-flow: row wrap;
}

.stock-display {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  width: 300px;
  height: 300px;
  margin: 5px;
  flex-grow: 1 0 33%;
}

.stock-display:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

.stock-overview {
  flex-grow: 1;
  border: 2px solid black;
  border-radius: 20px;
  margin: 10px;
  display: flex;
  justify-content: center;
  background-color: whitesmoke;
}

.stock-text {
  justify-self: center;
  align-self: center;
  font-size: 25px;
  text-align: center;
}

.stock-overview a {
  color: #3d3d3d;
  text-decoration: none;
}

main.stock-symbol-details {
  display: flex;
  padding: 15px;
  flex-flow: column wrap;
  height: 550px;
  width: 550px;
  margin: 0 auto;
  margin-top: 90px;
  background-color: whitesmoke;
  border-radius: 20px;
}

h1.loading {
  text-align: center;
}

div.symbol {
  justify-content: center;
  background-color: whitesmoke;
}

h3.name-symbol {
  border-bottom: 2px solid black;
  font-size: 23px;
}

ul.details * {
  list-style-type: none;
  margin-top: 18px;
  font-size: 20px;
}

li::before {
  content: "\1F4CA";
  margin-left: -15px;
  margin-right: 5px;
}

.exchange::before {
  content: "\1F4B1";
  margin-left: -15px;
  margin-right: 5px;
}

.high::before {
  content: "\1F4C8";
  margin-left: -15px;
  margin-right: 5px;
}

.low::before {
  content: "\1F4C9";
  margin-left: -15px;
  margin-right: 5px;
}

/* Daily Low - low U+1F4C9
Daily High - high U+1F4C8
52 Low - week52Low U+1F4C9
52 High - week52High U+1F4C8
Open - open
Close - close
Change - change U+1F4CA
Exchange - primaryExchange U+1F4B1 */

div.about-content {
  display: flex;
  justify-content: center;
}

div.about {
  height: 700px;
  width: 900px;
  background-color: whitesmoke;
  display: flex;
  justify-content: center;
  padding: 25px;
  margin-top: 40px;
  border-radius: 20px;
}

.about > p {
  font-size: 23px;
  line-height: 2;
}


